@import url("https://fonts.googleapis.com/css?family=Ubuntu");
body,html{font-family:'Ubuntu', sans-serif;  margin:0 auto;
    height:100vh;
    overflow: hidden;
    background:-webkit-radial-gradient(bottom, circle, #1B2735 20%, #090A0F 80%);
    background:-o-radial-gradient(bottom, circle, #1B2735 20%, #090A0F 80%);
    background:radial-gradient(circle at bottom, #1B2735 20%, #090A0F 80%);}
.menu{width:100%;height:100vh;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center}
.content{width:250px;text-align:center}input{font-family:inherit;font-size:125%}
.nick{width:165px;height:44px;border:none;padding:0 8px 0 8px}
.start{background:#fff;margin-top:20px;border:none;width:132px;height:44px}
.nickBar{
    position: absolute;
    right: .5em;
    top: .5em;
    color:white;

}
h1{
    color: #fff;
}
#left{
    width: 50vw;
    height: 100vh;
    position: absolute;
}
#right{
    width: 50vw;
    height: 100vh;
    position: absolute;
    left:50vw;
}


